import { defineComponent, reactive } from 'vue';
import { Picker, DayPicker, MonthPicker, YearPicker } from '../../src';
import { format, parse, sub } from 'date-fns';
import { it } from 'date-fns/locale';
export default defineComponent({
    components: {
        Picker: Picker,
        DayPicker: DayPicker,
        MonthPicker: MonthPicker,
        YearPicker: YearPicker,
    },
    setup: function () {
        var state = reactive({
            dayPicker: new Date(),
            monthPicker: new Date(),
            yearPicker: new Date(),
            picker: new Date(),
            pickerInput: null,
            disablePicker: new Date(),
            customDayPicker: new Date(),
            slotsPicker: new Date(),
            slotsRef: new Date(),
        });
        function parseDateString(dateString) {
            return parse(dateString, 'yyyy-MM-dd', new Date());
        }
        var disabled = {
            end: sub(new Date(), { days: 5 }),
        };
        function toNow() {
            state.slotsRef = new Date();
        }
        return {
            format: format,
            state: state,
            parseDateString: parseDateString,
            it: it,
            disabled: disabled,
            toNow: toNow,
            sections: [
                {
                    title: 'Full picker',
                    link: 'full',
                },
                {
                    title: 'Custom locale',
                    link: 'locale',
                },
                {
                    title: 'With input element',
                    link: 'input',
                },
                {
                    title: 'Customizing with slots',
                    link: 'slots',
                },
                {
                    title: 'Day picker',
                    link: 'day',
                },
                {
                    title: 'Month picker',
                    link: 'month',
                },
                {
                    title: 'Year picker',
                    link: 'year',
                },
            ],
        };
    },
});
